/*global chrome*/
import {withRouter} from 'react-router-dom';
import React, {Component} from 'react';
import {Col, Row, Form, Button, Card, Alert} from "react-bootstrap";
import axios from "axios";
import { withCookies, Cookies } from 'react-cookie';
import Modal from 'react-bootstrap/Modal'



class SaveProfile extends Component {
  tmr;
	state = {
        profileName: '',
        saveProfileUrl: '',
        readySave: false,

    };

    componentDidMount() {
        this.tmr = setInterval(this.saveProfileFile,3000);
    };

    save_cookies = (id) => {
      chrome.cookies.getAll({}, (cookies)=> {
        let i = cookies.length;
        while ( i-- ) {
          delete cookies[i]['session'];
          delete cookies[i]['hostOnly'];
          delete cookies[i]['httpOnly'];
            if (cookies[i]['domain'] == 'account.aezakmi.run') {
              delete cookies[i];
            }          
        }

        localStorage.setItem('cookies-' + id, JSON.stringify(cookies));
      })
    };

    save_history = (id) => {
        chrome.history.search({text: ''}, (history)=> {
            let i = history.length;
            while ( i-- ) {
              delete history[i]['id'];
              delete history[i]['lastVisitTime'];
              delete history[i]['title'];
              delete history[i]['typedCount'];
              delete history[i]['visitCount'];
            }  
            localStorage.setItem('history-' + id, JSON.stringify(history));
        })
    };

    save_activeTabs = (id) => {
  		chrome.tabs.query({}, function (savetabs) {
  			var vkladki = {};
  			var taburls = {};
  			var i = 0;
  		    savetabs.forEach((savetab)=>{
  		        vkladki[i] = savetab;
  		        taburls[i] = vkladki[i].url
  		        i = i+1;
  		    })
  		    localStorage.setItem('activeTabs-' + id, JSON.stringify(taburls));
  		});
	  };

    pauser = (timems) => {
      function lol() {
        let adas = 1;
      }
      setTimeout(lol, timems);
    }

    saveProfileFile = async () => {
        this.setState({profileName: JSON.parse(localStorage.getItem('userProfile')).profileName})
        if (JSON.parse(localStorage.getItem('userProfile')) !== null) {
            let currentFingerprintNameForSaveUserData = JSON.parse(localStorage.getItem('userProfile')).profileName.replace(/ /g, "_");
            await this.save_history(currentFingerprintNameForSaveUserData);
            await this.save_cookies(currentFingerprintNameForSaveUserData);
            await this.save_activeTabs(currentFingerprintNameForSaveUserData);
        }        

        var archive = {},
            keys = Object.keys(localStorage),
            i = keys.length;

        while ( i-- ) {
            archive[ keys[i] ] = localStorage.getItem( keys[i] );
        }


        var currentLocalStorage = {};
        currentLocalStorage.userProfile = archive.userProfile;


        currentLocalStorage['activeTabs-' + this.state.profileName.replace(/ /g, "_")] = archive['activeTabs-' + this.state.profileName.replace(/ /g, "_")];
        currentLocalStorage['history-' + this.state.profileName.replace(/ /g, "_")] = archive['history-' + this.state.profileName.replace(/ /g, "_")];
        currentLocalStorage['cookies-' + this.state.profileName.replace(/ /g, "_")] = archive['cookies-' + this.state.profileName.replace(/ /g, "_")];
        currentLocalStorage['started'] = archive['started'];
        currentLocalStorage['inProcess'] = archive['inProcess'];
        currentLocalStorage['profileHash'] = archive['profileHash'];


        var savedProfileName = JSON.parse(currentLocalStorage.userProfile).profileName.replace(/ /g, "_");


        if (currentLocalStorage['cookies-' + savedProfileName] !== null && currentLocalStorage['history-' + savedProfileName] !== null && currentLocalStorage['activeTabs-' + savedProfileName] !== null  ) {
          await this.pauser(10000)
          this.setState({readySave: true});
        } else {
          this.setState({readySave: false});
        }
        var encodedLocalStorageOne = btoa(JSON.stringify(currentLocalStorage));
        var encodedLocalStorage = btoa(encodedLocalStorageOne);
        var declLink = "data:application/octet-stream;charset=utf-16le;base64,"
        this.setState({saveProfileUrl: declLink + encodedLocalStorage});

    };
   



    render() {
        return(
        	<div className="bg">
		        <Modal show={true}>
			        <Modal.Header>
			          <Modal.Title>Save Profile File</Modal.Title>
			        </Modal.Header>

		        	<Modal.Body>
              {this.state.readySave ? <Button target="_blank" download={this.state.profileName + '-profile.aezakmi'} href={this.state.saveProfileUrl} variant="dark">Download</Button> : <Button variant="dark">WAIT...</Button> }
                              
		        	</Modal.Body>

			        <Modal.Footer>
			        </Modal.Footer>
		      	</Modal>
        	</div>
        )
    }
}


export default withCookies(withRouter(SaveProfile));
