import Dashboard from "../views/Dashboard";
import AuthForm from "../views/AuthForm";
import UploadProfile from "../views/UploadProfile";
import SaveProfile from "../views/SaveProfile";
import UploadCookie from "../views/UploadCookie";
import Aezakmi from "../views/Aezakmi";
import Facebook from "../views/Facebook";
import GeoSettings from "../views/GeoSettings";
import ProxySettings from "../views/ProxySettings";


const BaseRouter = [
    {
        path: '/dashboard',
        name: 'Home',
        component: Dashboard,
    },
    {
        path: '/auth',
        name: 'auth',
        component: AuthForm,
    },
    {
        path: '/upload',
        name: 'upload',
        component: UploadProfile,
    },
    {
        path: '/saveprofile',
        name: 'saveprofile',
        component: SaveProfile,
    },
    {
        path: '/uploadcookie',
        name: 'uploadcookie',
        component: UploadCookie,
    },
    {
        path: '/aezakmi',
        name: 'aezakmi',
        component: Aezakmi,
    },
    {
        path: '/facebook',
        name: 'facebook',
        component: Facebook,
    },
    {
        path: '/geosettings',
        name: 'geosettings',
        component: GeoSettings,
    },
    {
        path: '/proxysettings',
        name: 'proxysettings',
        component: ProxySettings,
    }          
         

];
export default BaseRouter;