/*global chrome*/
import React, {Component} from 'react';
import {Col, Row, Form, Button, Card, Alert} from "react-bootstrap";
import { withCookies, Cookies } from 'react-cookie';
import {withRouter} from 'react-router-dom';
import Header from './Header';

const crypto = require('crypto');




class Facebook extends Component {
    tmr;
    tmr2;
    profileSelect = React.createRef();
    state = {
        authString: '',
        login:'',
      
        fb_token_ico: 'fb_token.png',
        bm_settings_ico: 'bm_settings.png',
        fp_create_ico: 'fp_create.png',
        card_check_ico: 'card_check.png',         
        quick_mod_ico: 'quick_mod.png',
        auto_rules_ico: 'auto_rules.png',           


    };

    componentWillMount() {
       let authString = this.props.cookies.get('auth');
       if(authString !== undefined) {
           let login = this.props.cookies.get('login');
           this.setState({authString: authString, login: login});
       }
       else {
           this.props.history.push("/auth");
       }
       

    }

    componentDidMount() {     
    };


    getFbToken = () => {
        var fbRequest = {};
        fbRequest.method = "fbtoken"
        this.toBackgroundMessage(fbRequest);
    };

    toBackgroundMessage = (object) => {
        chrome.runtime.sendMessage(object,
        function (response) {});
    }

    openLink = (url) => {
        window.open(url, '_blank');
    }

    cardCheckPoint = () => {
        var fbRequest = {};
        fbRequest.method = "fbcardcheck"
        this.toBackgroundMessage(fbRequest);        
    }   
 
    
    render() {

        return (
            <div>
                <Header login = {this.state.login} />
                    <Alert variant="primary"><h4  className={'text-center'}>Facebook Tools</h4></Alert>
                    <Row className="justify-content-center">
                        <Col>
                        &nbsp;
                        </Col>                     
                    </Row>                    
                    <Row className= "justify-content-center" style={{textAlign: 'center', color: 'black', fontWeight: 500}}>
                        <Col>
                            <img id="fbtoken" src={this.state.fb_token_ico} onMouseEnter={() => {this.setState({fb_token_ico: "fb_token_hower.png"});}} onMouseOut={() => {this.setState({fb_token_ico: "fb_token.png"});}} title="Get FB Ads Token" onClick = {this.getFbToken}/>
                            <label for="fbtoken">FB Token</label>
                        </Col>
                        <Col>
                            <img id="bmsettings" src={this.state.bm_settings_ico} onMouseEnter={() => {this.setState({bm_settings_ico: "bm_settings_hower.png"});}} onMouseOut={() => {this.setState({bm_settings_ico: "bm_settings.png"});}} title="FB BM Settings" onClick = {this.openLink.bind(this,"https://business.facebook.com/settings/")}/>
                            <label for="bmsettings">BM Settings</label>
                        </Col>
                        <Col>
                            <img id="fbcreate" src={this.state.fp_create_ico} onMouseEnter={() => {this.setState({fp_create_ico: "fp_create_hower.png"});}} onMouseOut={() => {this.setState({fp_create_ico: "fp_create.png"});}} title="FB Create FP" onClick = {this.openLink.bind(this,"https://www.facebook.com/pages/creation/")}/>
                            <label for="fbcreate">Create Fun Page</label>
                        </Col>                                                                      
                    </Row>
                    <Row className="justify-content-center">
                        <Col>
                        &nbsp;
                        </Col>                     
                    </Row>
                    <Row className="justify-content-center" style={{textAlign: 'center', color: 'black', fontWeight: 500}}>
                        <Col>
                            <img id="cardcheck" src={this.state.card_check_ico} onMouseEnter={() => {this.setState({card_check_ico: "card_check_hower.png"});}} onMouseOut={() => {this.setState({card_check_ico: "card_check.png"});}} title="FB Card ChekPoint" onClick = {this.cardCheckPoint}/>
                            <label for="cardcheck">Card Check</label>
                        </Col>
                        <Col>
                            <img id="quickmod" src={this.state.quick_mod_ico} onMouseEnter={() => {this.setState({quick_mod_ico: "quick_mod_hower.png"});}} onMouseOut={() => {this.setState({quick_mod_ico: "quick_mod.png"});}} title="FB Quick Mode" onClick = {this.openLink.bind(this,"https://www.facebook.com/help/contact/186912391909649")}/>
                            <label for="quickmod">Quick Moderation</label>
                        </Col>
                        <Col>
                            <img id="autorules" src={this.state.auto_rules_ico} onMouseEnter={() => {this.setState({auto_rules_ico: "auto_rules_hower.png"});}} onMouseOut={() => {this.setState({auto_rules_ico: "auto_rules.png"});}} title="FB AutoRules" onClick = {this.openLink.bind(this,"https://business.facebook.com/ads/manager/rules/management/")}/>
                            <label for="autorules">Auto Rules</label>
                        </Col>                                              
                    </Row>

                    <Button href="/index.html#dashboard" style={{position: 'fixed', left: 0, bottom: 0}} variant="info"><i className="material-icons" style = {{color: "black"}}>arrow_back</i></Button>

            </div>
        );
    }
}

export default withCookies(withRouter(Facebook));
