import React, { Component } from 'react';
import { HashRouter, Route, Switch } from 'react-router-dom';
// import logo from './logo.svg';
import './css/bootstrap.css';
import './App.css';
import Dashboard from "./views/Dashboard"
import AuthForm from "./views/AuthForm";
import UploadProfile from "./views/UploadProfile";
import SaveProfile from "./views/SaveProfile";
import UploadCookie from "./views/UploadCookie";
import Aezakmi from "./views/Aezakmi";
import Facebook from "./views/Facebook";
import GeoSettings from "./views/GeoSettings";
import ProxySettings from "./views/ProxySettings";

import BaseRouter from './routes/BaseRouter'

import {Container} from "react-bootstrap";
import { CookiesProvider } from 'react-cookie';

class App extends Component {
  render() {
    return (
        <CookiesProvider>
        <Switch>
          <Route exact path = "/" component = {AuthForm} />
            {BaseRouter.map((props) => <Route path = {props.path} component = {props.component} />)}
        </Switch>
        </CookiesProvider>
    );
  }
}

export default App;
