/*global chrome*/
import React, {Component} from "react";
import { withCookies, Cookies } from 'react-cookie';
import {withRouter} from 'react-router-dom';
import {Button, Nav, Navbar, Image} from "react-bootstrap";
import 'material-design-icons/iconfont/material-icons.css';
import { LinkContainer } from 'react-router-bootstrap';


class Header extends Component {

    logOut = async() => {
        localStorage.setItem('started', 'false');
        localStorage.setItem('inProcess', 'false');
        localStorage.setItem('profileHash', '');
        var toBackgroundMessage = {}
        toBackgroundMessage.method = 'messageLogout';
        await chrome.runtime.sendMessage(toBackgroundMessage, function (response) {});
        this.props.cookies.remove('auth');
        this.props.cookies.remove('login');
        this.props.history.push("/auth");
    };

    saveSession = () => {
        if (JSON.parse(localStorage.getItem('userProfile')) !== null) {
            var toBackgroundMessage = {};
            toBackgroundMessage.method = 'messageSaveSession';
            chrome.runtime.sendMessage(toBackgroundMessage, function (response) {
            });
        }        
    } 



    render() {
        return (
            <Navbar >
                <div>
                <a target="_blank" href="https://aezakmi.run">
                <img src="logo.png" style = {{height: 30}}>
                </img>
                </a>
                </div>
                <Nav className="mr-auto">
                </Nav>
                <span style = {{paddingTop: 5}}>
                <h5>
                <strong>{this.props.login.substring(0, 4)}...</strong>
                </h5>
                </span>
                <Button title="Log Out" onClick = {this.logOut} size={'sm'} style = {{paddingTop: 5, paddingRight: 5, paddingBottom: 0, paddingLeft: 5, backgroundColor: 'transparent', border: 'none'}}>
                <i className="material-icons" style = {{color: "black"}}>exit_to_app</i>
                </Button>
                <Button style={{float: 'left', color: 'yellow'}} className="material-icons" title="Save Your Session Before Exit" onClick={this.saveSession} variant={'success'}>save</Button>
            </Navbar>
        );
    }
}

export default withCookies(withRouter(Header));
