/*global chrome*/
import React, {Component} from 'react';
import {Col, Row, Form, Button, Card, Alert} from "react-bootstrap";
import { withCookies, Cookies } from 'react-cookie';
import {withRouter} from 'react-router-dom';
import Header from './Header';
import axios from "axios";
import LangTimeJSON from '../libs/langtime.json'




class GeoSettings extends Component {
    profileSelect = React.createRef();
    state = {
        authString: '',
        login:'',
      
        accept_language: '',
        language_Const1: '',
        languageConst1: '',
        sysTimeRegion: '',
        sysTimeConst: '',
        gpslat: '',
        gpslon: '',
        daylightOffset: '',
        countriesArrayState: [],
        timezonesArrayState: [],
        selectedCountry: '',
        selectedTimezone: '',
        profileSaved: false,
        langTimeObjects: [],

    };

    componentWillMount() {
       let authString = this.props.cookies.get('auth');
       if(authString !== undefined) {
           let login = this.props.cookies.get('login');
           this.setState({authString: authString, login: login});
       }
       else {
           this.props.history.push("/auth");
       }      
    }

    componentDidMount() {
      this.getCountryList();
      this.parseGeoSettings();
    }

    //     
    getCountryList = () => {
      let countriesArray = [];
      LangTimeJSON.forEach((item) => {    
          countriesArray.push(item.country)
      });

      let result = [];

      for (let str of countriesArray) {
        if (!result.includes(str)) {
          result.push(str);
        }
      }

      this.setState({countriesArrayState: result.sort()})
    
    }

    // -     
    getTimezonesList = () => {
      let timezonesArray = [];
      LangTimeJSON.forEach((item) => {
        if (item.country == this.state.selectedCountry) {
              timezonesArray.push(item.timezone)
          }
      });
      this.setState({timezonesArrayState: timezonesArray.sort()})  
   
    }

    //     ,       
    findLangTime = () => {
      let selectedLangTime = {};
      LangTimeJSON.forEach((item) => {
        if (item.country == this.state.selectedCountry && item.timezone == this.state.selectedTimezone) { 
              selectedLangTime = item;
          }
      });

      this.setState({
            accept_language: selectedLangTime.accept_language,
            language_Const1: selectedLangTime.nav_lang,
            languageConst1: selectedLangTime.nav_langs,
            sysTimeRegion: selectedLangTime.timezone,
            gpslat: selectedLangTime.gps[0],
            gpslon: selectedLangTime.gps[1]
      });       

      axios.post(process.env.REACT_APP_PUBLIC_API + '/api/get_time_offset', {
          'timeZone': selectedLangTime.timezone,
          'date': Date.now()
      }).then((res) => {
          this.setState({
                daylightOffset: res.data.offset
          }); 
      })

    }

    changeForm = (e) => {
        let id = e.currentTarget.id;
        let value = e.currentTarget.value;

        switch (id) {
            case "countriesArray":

                this.setState(()=>({selectedCountry: value}), ()=> {

                  this.getTimezonesList();
                });                
            break;
            case "timezonesArray":

                this.setState(()=>({selectedTimezone: value}), ()=> {

                  this.findLangTime();
                });
            break;
        }
    };

    changeInputs = (e) => {
      let id = e.currentTarget.id;    
      let value = e.currentTarget.value;
      switch (id) {
            case "acceptLanguage":
                 this.setState(()=>({accept_language: value}), ()=> {});
            break;
            case "allLanguages":
                 this.setState(()=>({languageConst1: value}), ()=> {});
            break;
            case "mainLanguage":
                 this.setState(()=>({language_Const1: value}), ()=> {});
            break;
            case "timezoneRegion":
                 this.setState(()=>({sysTimeRegion: value}), ()=> {});
            break;
            case "momentOffset":
                 this.setState(()=>({daylightOffset: value}), ()=> {});
            break;
            case "gpslat":
                 this.setState(()=>({gpslat: value}), ()=> {});
            break;
            case "gpslon":
                 this.setState(()=>({gpslon: value}), ()=> {});
            break;                                                                                                               
      }
    }    

    parseGeoSettings = () => {
      try {
        let userProfile = JSON.parse(localStorage.getItem('userProfile'));

        this.setState({
            accept_language: userProfile.accept_language,
            language_Const1: userProfile.language_Const1,
            languageConst1: userProfile.languageConst1,
            sysTimeRegion: userProfile.sysTimeRegion,
            sysTimeConst: userProfile.sysTimeConst,
            gpslat: userProfile.gpslat,
            gpslon: userProfile.gpslon,
            daylightOffset: userProfile.daylightOffset,
            profileName: userProfile.profileName
        });
      } catch (err) {}       
    }

    setGeoSettings = () => {
        let userProfile = JSON.parse(localStorage.getItem('userProfile'));

        if (this.state.accept_language !== '' && this.state.language_Const1 !== '' && this.state.languageConst1 !== '' && this.state.sysTimeRegion !== '' && this.state.sysTimeConst !== '' && this.state.gpslat !== '' && this.state.gpslon !== '' && this.state.daylightOffset !== '') {
            userProfile.accept_language = this.state.accept_language;
            userProfile.language_Const1 = this.state.language_Const1;
            userProfile.languageConst1 = this.state.languageConst1;
            userProfile.sysTimeRegion = this.state.sysTimeRegion;
            userProfile.sysTimeConst = this.state.sysTimeConst;
            userProfile.gpslat = this.state.gpslat;
            userProfile.gpslon = this.state.gpslon;
            userProfile.daylightOffset = this.state.daylightOffset;


            localStorage.setItem('userProfile', JSON.stringify(userProfile));

            let d = {'method': 'updateLangTime'};
            this.toBackgroundMessage(d);
          axios.post(process.env.REACT_APP_PUBLIC_API + '/api/modify_user_profile', {
              'profileName': this.state.profileName,
              'login': this.state.login,
              'accept_language': this.state.accept_language,
              'language_Const1': this.state.language_Const1,
              'languageConst1': this.state.languageConst1,
              'sysTimeRegion': this.state.sysTimeRegion,
              'gpslat': this.state.gpslat,
              'gpslon': this.state.gpslon,
              'daylightOffset': this.state.daylightOffset,
              'method': "geo"
          }, {
              headers: {
                  'Authorization': this.state.authString,
              }
          }) 
        }
    }

    toBackgroundMessage = (object) => {
        chrome.runtime.sendMessage(object,
        function (response) {});
    }
  
    render() {

        return (
            <div className="bg-free">
                <Header login = {this.state.login} />
                    <Alert variant="primary"><h4 className={'text-center'}>Geolocation Settings</h4></Alert>
                    
                            <Row>

                                <Col md={12}>
                                  <Form.Label className='text-mono'>
                                    Country
                                  </Form.Label>
                                  <Form.Control className='prof-select' value={this.state.selectedCountry} id='countriesArray' as="select" onChange={this.changeForm}>
                                    <option></option>
                                    {this.state.countriesArrayState.map((item) => <option value={item}>{item}</option>)}
                                  </Form.Control>
                                </Col>

                                <Col md={12}>
                                  <Form.Label className='text-mono'>
                                    TimeZone
                                  </Form.Label>
                                  <Form.Control className='prof-select' value={this.state.selectedTimezone} id='timezonesArray' as="select" onChange={this.changeForm}>
                                    <option></option>
                                    {this.state.timezonesArrayState.map((item) => <option value={item}>{item}</option>)}
                                  </Form.Control>
                                </Col>

                            </Row>

                            <Row>

                                <Form.Group as={Col} md="1">
                                    <Form.Label>Accept Language</Form.Label>
                                    <Form.Control
                                                  onChange={this.changeInputs}
                                                  value={this.state.accept_language} id='acceptLanguage' type="text"/>
                                </Form.Group> 

                                <Form.Group as={Col} md="1">
                                    <Form.Label>All Languages</Form.Label>
                                    <Form.Control
                                                  onChange={this.changeInputs}
                                                  value={this.state.languageConst1} id='allLanguages' type="text"/>
                                </Form.Group>

                                <Form.Group as={Col} md="1">
                                    <Form.Label>Main Language</Form.Label>
                                    <Form.Control
                                                  onChange={this.changeInputs}
                                                  value={this.state.language_Const1} id='mainLanguage' type="text"/>
                                </Form.Group> 

                            </Row>

                            <Row>

                                <Form.Group as={Col} md="6">
                                    <Form.Label>TimeZone</Form.Label>
                                    <Form.Control
                                                  onChange={this.changeInputs}
                                                  value={this.state.sysTimeRegion} id='timezoneRegion' type="text"/>
                                </Form.Group> 

                                <Form.Group as={Col} md="6">
                                    <Form.Label>Time Offset</Form.Label>
                                    <Form.Control
                                                  onChange={this.changeInputs}
                                                  value={this.state.daylightOffset} id='momentOffset' type="text"/>
                                </Form.Group>

                            </Row>

                            <Row>

                                <Form.Group as={Col} md="4">
                                    <Form.Label>GPS latitude</Form.Label>
                                    <Form.Control
                                                  onChange={this.changeInputs}
                                                  value={this.state.gpslat} id='gpslat' type="text"/>
                                </Form.Group> 

                                <Form.Group as={Col} md="4">
                                    <Form.Label>GPS longitude</Form.Label>
                                    <Form.Control
                                                  onChange={this.changeInputs}
                                                  value={this.state.gpslon} id='gpslon' type="text"/>
                                </Form.Group>

                                <Col md={4}>
                                  <Button variant={'success'} block onClick={this.setGeoSettings}>Set New Geo-Settings</Button>
                                </Col>

                            </Row>

                            <Row>
                              <Col md={4}><br /></Col>
                            </Row>                                                         


                    <Button href="/index.html#dashboard" style={{position: 'fixed', left: 0, bottom: 0}} variant="info"><i className="material-icons" style = {{color: "black"}}>arrow_back</i></Button>

            </div>
        );
    }
}

export default withCookies(withRouter(GeoSettings));