/*global chrome*/

import {withRouter} from 'react-router-dom';
import React, {Component} from 'react';
import {Col, Row, Form, Button, Card, Alert} from "react-bootstrap";
import axios from "axios";
import { withCookies, Cookies } from 'react-cookie';



class AuthForm extends Component {
    state = {
        login: '',
        password: '',
        chPassword: true,
        chLogin: true,
        auth: false,
        firstStart: true,
        resMessage: '',
        lastSession: false,
        restoreStatus: false,
    };

    changeInputs = (e) => {
        let id = e.currentTarget.id;
        let value = e.currentTarget.value;
        switch (id) {
            case 'login':
                this.setState({login: value});
                break;
            case 'password':
                this.setState({password: value});
        }
    };

    componentWillMount() {
        let authString = this.props.cookies.get('auth');

        if(authString !== undefined) {
            this.props.history.push("/dashboard");
        }
        else {
            this.props.history.push("/auth");
        }
    }
    componentDidMount() {
        this.lastSessionCheck();
    }

    onLogin = (e) => {
        const form = e.currentTarget;
        axios.post(process.env.REACT_APP_PUBLIC_API + '/api/auth', {
            'login': this.state.login,
            'password': this.state.password,
        }).then((res)=> {
            e.preventDefault();
                this.setState(()=> ({
                    chPassword: res.data.password,
                        chLogin: res.data.login,
                        auth: res.data.done,
                    resMessage: res.data.message,
                    firstStart: false,
                }), ()=> {

                    form.checkValidity();
                    if(this.state.auth) {
                        localStorage.setItem('started', 'false');
                        localStorage.setItem('inProcess', 'false');
                        localStorage.setItem('profileHash', '');
                        this.props.cookies.set('auth', res.data.auth, {path: '/'});
                        localStorage.setItem('datetime', res.data.auth);
                        this.props.cookies.set('login', this.state.login, {path: '/'});
                        localStorage.setItem('login', this.state.login);
                        this.props.history.push("/dashboard");
                    }
                })


        }).catch((err)=> {
            this.setState({auth: false, resMessage: 'server is unavailable', firstStart: false});
        })
    };

    onEnterLogin = (e) => {
        if(e.key === "Enter") {
            this.onLogin(e);
        }
    };

// Only for MacOS

    lastSessionCheck = () => {
        if (JSON.parse(localStorage.getItem('userProfile')) !== null) {
            var id = JSON.parse(localStorage.getItem('userProfile')).profileName.replace(/ /g, "_");

            if(localStorage.getItem('session-' + id) == '') {
                this.setState({ lastSession: true });
            } else {
                this.setState({ lastSession: false });
            }
        } else {
                this.setState({ lastSession: false });
        }
    }

    restoreSession = () => {
        if (this.state.lastSession) {
            var toBackgroundMessage = {};
            toBackgroundMessage.method = 'messageRestoreSession';
            chrome.runtime.sendMessage(toBackgroundMessage, function (response) {
            });
                this.setState({ restoreStatus: true });
                var id = JSON.parse(localStorage.getItem('userProfile')).profileName.replace(/ /g, "_");
                var datetime = localStorage.getItem('datetime');
                var cur_login = localStorage.getItem('login');
                this.props.cookies.set('auth', datetime, {path: '/'});
                this.props.cookies.set('login', cur_login, {path: '/'});
                this.props.history.push("/dashboard");

        }        
    }
// __end Only for MacOS

    render() {
        return(
            <div>
               <Row className = 'justify-content-center' style={{marginTop: 50}}>

                <Form noValidate validated={!this.state.firstStart ? this.state.auth : false}>

                    <Form.Group>

                        <p className = 'text-center'>Login</p>

                        <Form.Control onKeyPress={(e)=> this.onEnterLogin(e)} required isInvalid = {!this.state.firstStart ? !this.state.auth : false} onChange = {this.changeInputs} value = {this.state.login} id = 'login' type="text" />
                        
                        <p className = 'text-center'>Password</p>

                        <Form.Control onKeyPress={(e)=> this.onEnterLogin(e)} required isInvalid = {!this.state.firstStart ? !this.state.auth : false} onChange = {this.changeInputs} value = {this.state.password} id = 'password' type="password" />

                        <Form.Control.Feedback className = 'text-center' type="invalid">
                            <span >{this.state.resMessage}</span>
                        </Form.Control.Feedback>

                        <Button onClick = {this.onLogin} block >Log in</Button>

                        <Button variant={'success'} block disabled = {!this.state.lastSession} onClick = {this.restoreSession}>Restore Session</Button>
                        
                        {this.state.restoreStatus ? 
                            <Alert key="warning" variant="warning">
                                <span>
                                    <span className="text-success">Cheat enabled</span>
                                </span>
                            </Alert> : ''}

                        

                    </Form.Group>

                </Form>
               </Row>
            </div>

        )
    }
}


export default withCookies(withRouter(AuthForm));