/*global chrome*/
import {withRouter} from 'react-router-dom';
import React, {Component} from 'react';
import {Col, Row, Form, Button, Card, Alert, Table} from "react-bootstrap";
import axios from "axios";
import { withCookies, Cookies } from 'react-cookie';
import Modal from 'react-bootstrap/Modal'
import Header from './Header';

const crypto = require('crypto');



class Aezakmi extends Component {
    tmr;
    tmr2;
    profileSelect = React.createRef();
    state = {

        profileName: '',
        authString: '',
        login:'',

        currentUserProfile: {},
        userProfileInfo: [],
        userProfileReady: false,
        profilesLoaded: true,

        names: [],
        namesWithCookies: [],
        namesToRemove: [],

        wait30second: false,
        toRemoveTab: '',
        fromBackground: {},
        licenseType: '',
        days: '',
        isLicenseActive: true,
        accessManageProfileFilesState: false,
        proxyProfileName: "", 
        proxyStatus: null,

    };


    componentWillMount() {
       let authString = this.props.cookies.get('auth');
       if(authString !== undefined) {
           let login = this.props.cookies.get('login');
           this.setState({authString: authString, login: login});
       }
       else {
           this.props.history.push("/auth");
       }
       

    }

    componentDidMount() {
        this.becomeAllUserProfiles();
        this.license();        
    };   


    localStorageCheck = (profileName) => {
        var cookieCheck = localStorage.getItem('cookies-' + profileName);
        if (cookieCheck === null) {
            return false;
        } else {
            return true;
        }
    }

    chekedProfiles = (profileName) => {        

        let profiles = this.state.namesToRemove;

        if (profiles.indexOf(profileName) !== -1) {
            profiles.splice(profiles.indexOf(profileName), 1);
            this.setState({namesToRemove: profiles})
        } else {
            profiles.push(profileName);
            this.setState({namesToRemove: profiles})
        }
        console.log(this.state.namesToRemove)

    }

    removeProfiles = () => {
        axios.post(process.env.REACT_APP_PUBLIC_API + '/api/remove_profiles', {login: this.state.login, profiles: this.state.namesToRemove}, {
            headers: {
                'Authorization': this.state.authString,
            }
        }).then(res => {
            window.location.reload()
        })        
    }

    license = async () => {
        await axios.post(process.env.REACT_APP_PUBLIC_API + '/api/vashi_documenti', {login: this.state.login}, {
            headers: {
                'Authorization': this.state.authString,
            }
        }).then(res => {

            this.setState({licenseType: res.data.license, days: res.data.days})
            if (res.data.days = 0) {
                this.setState({isLicenseActive: false})
            }
            if (res.data.days > 0) {
                this.setState({isLicenseActive: true})
            }
        })

        this.accessManageProfileFiles();       
    }


    toBackgroundMessage = (object) => {
        chrome.runtime.sendMessage(object,
        function (response) {});
    }

    becomeUserProfile = (profileName, e) => {
        this.setState({profileReady: false}); 

        axios.post(process.env.REACT_APP_PUBLIC_API + '/api/get_current_user_profile', {
            'login': this.state.login,
            'profileName': profileName,
        }, {headers: {
                'Authorization' : this.state.authString,
            }}).then(async (res)=> {
                let d = res.data[0];
                let isProfileNew = this.checkProfileHash(JSON.stringify(d));
                if(isProfileNew) {
                d.method = 'messageBecomeUserProfile';
                await this.toBackgroundMessage(d);

                    this.setState( ()=> ({currentUserProfile: d, profileName: d.profileName}), ()=> {
                        this.start();
                    });

                }

            })
    };

    checkProfileHash = (str) => {
        let oldHash = localStorage.getItem('profileHash');
        const hash = crypto.createHash('sha256');
        hash.update(str);
        let hashStr = hash.digest('hex');
        if(oldHash !== hashStr) {
            localStorage.setItem('profileHash', hashStr);
            return true;
        }
        else {
            return false;
        }

    }    

    becomeAllUserProfiles = () => {

        axios.post(process.env.REACT_APP_PUBLIC_API + '/api/get_all_user_profiles_advanced', {
            'login': this.state.login,
        }, {headers: {
                'Authorization' : this.state.authString,
            }}).then((res)=> {
                let names = [];
                let namesWithCookies = [];
                res.data.forEach((item)=> {
                    if(item.profileName !== undefined) {                        
                        if (this.localStorageCheck(item.profileName)) {
                            namesWithCookies.push(item)
                        } else {
                            names.push(item)
                        }
                    }
                });
            this.setState({userProfileInfo: res.data, names: names, namesWithCookies: namesWithCookies, profilesLoaded: true});
        }).catch((err)=> {
            this.setState({profilesLoaded: false})
        })
    };
  

    start = () => {
        this.setState({started: true});
        localStorage.setItem('started', 'true');
        localStorage.setItem('inProcess', 'false');
    };


    accessManageProfileFiles = () => {
        if (this.state.licenseType == 'passenger' || this.state.licenseType == 'betting') {
            this.setState({accessManageProfileFilesState: false});
        }
        if (this.state.licenseType == 'grovestreet' || this.state.licenseType == 'cj' || this.state.licenseType == 'ballas'){
            this.setState({accessManageProfileFilesState: true});
        }
    }


    proxyChecker = (profileName, proxyProtocol, proxyIp, proxyPort, proxyUser, proxyPassword, e) => {
        if (proxyIp.indexOf('.') > -1) {
            this.setState({proxyProfileName: profileName, proxyStatus: null});
            axios.post(process.env.REACT_APP_PUBLIC_API + '/api/check_proxy', {
                'login': this.state.login,
                'Protocol': proxyProtocol,
                'ip': proxyIp,
                'Port': proxyPort,
                'proxyUser': proxyUser,
                'proxyPassword': proxyPassword,
            }, {
                headers: {
                    'Authorization': this.state.authString,
                }
            }).then((res) => {

                if (res.data.code === 0 && res.data.ip != "") {
                    this.setState({proxyProfileName: profileName, proxyStatus: true});
                } else if (res.data.code === 1488 && res.data.ip == "") {
                    this.setState({proxyProfileName: profileName, proxyStatus: false});
                }
           });
        }
    };                   

    render() {
        return(
        	<div className="bg">
                <Header login = {this.state.login} />
                    <h4 className={'text-center'}>Select Profile</h4>
                            <Row className= "justify-content-center">
                                <Col md = {1}></Col>
                                <Col md = {2}>
                                    <Button variant={'danger'} disabled = {this.state.namesToRemove.length < 0} onClick = {this.removeProfiles}>Remove Profiles</Button>
                                </Col>
                                <Col md = {6}></Col>
                                <Col md = {2}>
                                    {/*<Button target="_blank"  href="http://account.aezakmi.run" variant="dark">Create New Profile</Button>*/}
                                </Col>                                 
                                <Col md = {1}></Col>                            
                            </Row>
                            <Row className= "justify-content-center">
                                <Col md = {1}></Col>
                                <Col md = {10}>

                                    <Alert className={this.state.isLicenseActive ? "d-none" : ""} key="warning" variant="warning">
                                            <span> Your License Expired! </span>
                                    </Alert>

                                    <Table striped bordered hover variant="light">
                                      <thead>
                                        <tr>
                                          <th></th>
                                          <th>#</th>
                                          <th>Profile Name</th>
                                          <th>Screen</th>
                                          <th>UserAgent</th>
                                          <th>Proxy</th>
                                          <th>Check Proxy</th>
                                          <th>Cookies</th>
                                          <th>Start Profile</th>
                                        </tr>
                                      </thead>

                                      <tbody>
                                        {this.state.userProfileInfo.length !== 0 ? this.state.namesWithCookies.map((item, i) => 
                                            <tr>
                                            <td><Form.Check onChange={this.chekedProfiles.bind(this, item.profileName)} /></td>
                                            <td>{i+1}</td>
                                            <td>{item.profileName}</td>
                                            <td>{item.widthvar}x{item.heightvar}</td>
                                            <td>{item.userAgentConst.indexOf('Windows') > -1 ? "Windows" : item.userAgentConst.indexOf('Linux') > -1 ? "Linux" : item.userAgentConst.indexOf('Android') > -1 ? "Linux" : item.userAgentConst.indexOf('Mac ') > -1 ? "Apple" : "???"}  {item.userAgentConst.indexOf('OPR') > -1 ? "Opera" : item.userAgentConst.indexOf('Chrome') > -1 ? "Chrome" : item.userAgentConst.indexOf('Firefox') > -1 ? "Firefox" : "???"}</td>
                                            <td>{item.proxyProtocol} {item.proxyIp !== null ? ` ${item.proxyIp} :` : ""} {item.proxyPort}</td>
                                            <td><Button variant={this.state.proxyProfileName == item.profileName && this.state.proxyStatus == null ? "warning" : this.state.proxyProfileName == item.profileName && this.state.proxyStatus == true ? "success" : this.state.proxyProfileName == item.profileName && this.state.proxyStatus == false ? "danger" : "secondary"} onClick={this.proxyChecker.bind(this, item.profileName, item.proxyProtocol, item.proxyIp, item.proxyPort, item.proxyUser, item.proxyPassword)}> {this.state.proxyProfileName == item.profileName && this.state.proxyStatus == null ? "" : this.state.proxyProfileName == item.profileName && this.state.proxyStatus == true ? "" : this.state.proxyProfileName == item.profileName && this.state.proxyStatus == false ? "X" : "Check"} </Button></td>
                                            <td><Button variant={this.localStorageCheck(item.profileName) ? "success" : "danger"}>{this.localStorageCheck(item.profileName) ? "" : "X"}</Button></td>
                                            <td><Button variant={'success'} block onClick={this.becomeUserProfile.bind(this, item.profileName)}>A-E-Z-A-K-M-I</Button></td>
                                            </tr>): null}

                                        {this.state.userProfileInfo.length !== 0 ? this.state.names.map((item, i) => 
                                            <tr>
                                            <td><Form.Check onChange={this.chekedProfiles.bind(this, item.profileName)} /></td>
                                            <td>{i+1}</td>
                                            <td>{item.profileName}</td>
                                            <td>{item.widthvar}x{item.heightvar}</td>
                                            <td>{item.userAgentConst.indexOf('Windows') > -1 ? "Windows" : item.userAgentConst.indexOf('Linux') > -1 ? "Linux" : item.userAgentConst.indexOf('Android') > -1 ? "Linux" : item.userAgentConst.indexOf('Mac ') > -1 ? "Apple" : "???"}  {item.userAgentConst.indexOf('OPR') > -1 ? "Opera" : item.userAgentConst.indexOf('Chrome') > -1 ? "Chrome" : item.userAgentConst.indexOf('Firefox') > -1 ? "Firefox" : "???"}</td>
                                            <td>{item.proxyProtocol} {item.proxyIp !== null ? ` ${item.proxyIp} :` : ""} {item.proxyPort}</td>
                                            <td><Button variant={this.state.proxyProfileName == item.profileName && this.state.proxyStatus == null ? "warning" : this.state.proxyProfileName == item.profileName && this.state.proxyStatus == true ? "success" : this.state.proxyProfileName == item.profileName && this.state.proxyStatus == false ? "danger" : "secondary"} onClick={this.proxyChecker.bind(this, item.profileName, item.proxyProtocol, item.proxyIp, item.proxyPort, item.proxyUser, item.proxyPassword)}> {this.state.proxyProfileName == item.profileName && this.state.proxyStatus == null ? "" : this.state.proxyProfileName == item.profileName && this.state.proxyStatus == true ? "" : this.state.proxyProfileName == item.profileName && this.state.proxyStatus == false ? "X" : "Check"} </Button></td>
                                            <td><Button variant={this.localStorageCheck(item.profileName) ? "success" : "danger"}>{this.localStorageCheck(item.profileName) ? "" : "X"}</Button></td>
                                            <td><Button variant={'success'} block onClick={this.becomeUserProfile.bind(this, item.profileName)}>A-E-Z-A-K-M-I</Button></td>
                                            </tr>): null}

                                      </tbody>

                                    </Table>
                                    
                                </Col>
                                <Col md = {1}></Col>

                            </Row>
        	</div>
        );
    }
}


export default withCookies(withRouter(Aezakmi));
