/*global chrome*/
import React, {Component} from 'react';
import {Col, Row, Form, Button, Card, Alert} from "react-bootstrap";
import { withCookies, Cookies } from 'react-cookie';
import {withRouter} from 'react-router-dom';
import Header from './Header';
import axios from "axios";




class ProxySettings extends Component {
    profileSelect = React.createRef();
    state = {
        authString: '',
        login:'',      
        proxyProtocol: '',
        proxyPort: '',
        proxyAddress: '',
        proxyLogin: '',
        proxyPassword: '',
        status911: false,
        proxyStatus: null,

    }

    componentWillMount() {
       let authString = this.props.cookies.get('auth');
       if(authString !== undefined) {
           let login = this.props.cookies.get('login');
           this.setState({authString: authString, login: login});
       }
       else {
           this.props.history.push("/auth");
       }      
    }

    componentDidMount() {
      this.parseProxySettings();
    }

    changeForm = (e) => {
        let id = e.currentTarget.id;
        let value = e.currentTarget.value;

        switch (id) {
            case "proxyProtocol":
                this.setState(()=>({proxyProtocol: value}), ()=> {});                
            break;

        }
    };    


    changeInputs = (e) => {
      let id = e.currentTarget.id;    
      let value = e.currentTarget.value;
      switch (id) {
            case "proxyAddress":
                 this.setState(()=>({proxyAddress: value}), ()=> {});
            break;
            case "proxyPort":
                 this.setState(()=>({proxyPort: value}), ()=> {});
            break;
            case "proxyLogin":
                 this.setState(()=>({proxyLogin: value}), ()=> {});
            break;
            case "proxyPassword":
                 this.setState(()=>({proxyPassword: value}), ()=> {});
            break;                                                                                                              
      }
    }

    parseProxySettings = () => {
      let userProfile = JSON.parse(localStorage.getItem('userProfile'));
      this.setState(()=>({
        proxyProtocol: userProfile.proxyProtocol,
        proxyPort: userProfile.proxyPort,
        proxyAddress: userProfile.proxyIp,
        proxyLogin:  userProfile.proxyUser,
        proxyPassword: userProfile.proxyPassword,
        profileName: userProfile.profileName,         
      }))
    }

    proxyChecker = () => {
            this.setState({proxyStatus: null});
            axios.post(process.env.REACT_APP_PUBLIC_API + '/api/check_proxy', {
                'login': this.state.login,
                'Protocol': this.state.proxyProtocol,
                'ip': this.state.proxyAddress,
                'Port': this.state.proxyPort,
                'proxyUser': this.state.proxyLogin,
                'proxyPassword': this.state.proxyPassword,
            }, {
                headers: {
                    'Authorization': this.state.authString,
                }
            }).then((res) => {

                if (res.data.code === 0 && res.data.ip != "") {
                    this.setState({proxyStatus: true});
                } else if (res.data.code === 1488 && res.data.ip == "") {
                    this.setState({proxyStatus: false});
                }
           });

    };

    change911 = () => {
      let newVal = !this.state.status911;
      this.setState({status911: newVal});
    }

    setProxy = () => {
      let userProfile = JSON.parse(localStorage.getItem('userProfile'));
      if (this.state.status911) {
          userProfile.enable911 = "1";
          userProfile.proxyProtocol = this.state.proxyProtocol;
          userProfile.proxyIp = this.state.proxyAddress;
          userProfile.proxyPort = this.state.proxyPort;
          userProfile.proxyUser = this.state.proxyLogin;
          userProfile.proxyPassword = this.state.proxyPassword;

          localStorage.setItem('userProfile', JSON.stringify(userProfile));
          let d = {'method': 'updateProxy'};
          this.toBackgroundMessage(d);           
      } else {
          axios.post(process.env.REACT_APP_PUBLIC_API + '/api/get_ip_info', {
              'ip': this.state.proxyAddress,
              'dateNow': Date.now(),
          }, {
              headers: {
                  'Authorization': "F490BD70F30ACA66A2BA8D00F40479E41CE945FC939A6919A95C73C7BBC26853",
              }
          }).then((res) => {
              userProfile.accept_language = res.data.lang.value;
              userProfile.language_Const1 = res.data.lang.lang;
              userProfile.languageConst1 = res.data.lang.langs;
              userProfile.sysTimeRegion = res.data.region;
              userProfile.gpslat = res.data.geo[0];
              userProfile.gpslon = res.data.geo[1];
              userProfile.daylightOffset = res.data.offset;
          }).then(() => {
            userProfile.proxyProtocol = this.state.proxyProtocol;
            userProfile.proxyIp = this.state.proxyAddress;
            userProfile.proxyPort = this.state.proxyPort;
            userProfile.proxyUser = this.state.proxyLogin;
            userProfile.proxyPassword = this.state.proxyPassword;

            localStorage.setItem('userProfile', JSON.stringify(userProfile));
            let d = {'method': 'updateProxy'};
            this.toBackgroundMessage(d);

          });
          axios.post(process.env.REACT_APP_PUBLIC_API + '/api/modify_user_profile', {
              'profileName': this.state.profileName,
              'login': this.state.login,
              'proxyProtocol': this.state.proxyProtocol,
              'proxyIp': this.state.proxyAddress,
              'proxyPort': this.state.proxyPort,
              'proxyUser': this.state.proxyLogin,
              'proxyPassword': this.state.proxyPassword,
              'accept_language': this.state.accept_language,
              'language_Const1': this.state.language_Const1,
              'languageConst1': this.state.languageConst1,
              'sysTimeRegion': this.state.sysTimeRegion,
              'gpslat': this.state.gpslat,
              'gpslon': this.state.gpslon,
              'daylightOffset': this.state.daylightOffset,              
              'method': "proxy"
          }, {
              headers: {
                  'Authorization': this.state.authString,
              }
          })          
      }



   
    }       

    toBackgroundMessage = (object) => {
        chrome.runtime.sendMessage(object,
        function (response) {});
    }
  
    render() {

        return (
            <div className="bg-free">
                <Header login = {this.state.login} />
                    <Alert variant="primary"><h4 className={'text-center'}>Proxy Settings</h4></Alert>
                      
                            <Row>

                            <Button variant={this.state.proxyStatus == null ? "warning" : this.state.proxyStatus == true ? "success" : this.state.proxyStatus == false ? "danger" : "secondary"} block disabled={this.state.status911} onClick={this.checkProxy}>CheckProxy</Button>

                                <Col md={12}>
                                  <Form.Label className='text-mono'>
                                    Protocol
                                  </Form.Label>
                                  <Form.Control className='prof-select' value={this.state.proxyProtocol} id='proxyProtocol' as="select" onChange={this.changeForm} disabled={this.state.status911}>
                                    <option value="http">http</option>
                                    <option value="https">https</option>
                                    <option value="socks4">socks4</option>
                                    <option value="socks5">socks5</option>
                                  </Form.Control>
                                </Col>

                                <Form.Group as={Col} md="1">
                                    <Form.Label>Address</Form.Label>
                                    <Form.Control disabled={this.state.status911}
                                                  onChange={this.changeInputs}
                                                  value={this.state.proxyAddress} id='proxyAddress' type="text"/>
                                </Form.Group> 

                                <Form.Group as={Col} md="1">
                                    <Form.Label>Port</Form.Label>
                                    <Form.Control 
                                                  onChange={this.changeInputs}
                                                  value={this.state.proxyPort} id='proxyPort' type="text"/>
                                </Form.Group> 

                                <Form.Group as={Col} md="1">
                                    <Form.Label>Login</Form.Label>
                                    <Form.Control disabled={this.state.proxyProtocol == "socks5" || this.state.proxyProtocol == "socks5" || this.state.status911}
                                                  onChange={this.changeInputs}
                                                  value={this.state.proxyLogin} id='proxyLogin' type="text"/>
                                </Form.Group> 
                                <Form.Group as={Col} md="1">
                                    <Form.Label>Password</Form.Label>
                                    <Form.Control disabled={this.state.proxyProtocol == "socks5" || this.state.proxyProtocol == "socks5" || this.state.status911}
                                                  onChange={this.changeInputs}
                                                  value={this.state.proxyPassword} id='proxyPassword' type="text"/>
                                </Form.Group>

                                <Form.Group>
                                  <Form.Check
                                    name="911"
                                    label="911"
                                    onChange={this.change911}
                                    checked={this.state.status911}
                                    id="is911"
                                  />
                                </Form.Group>                                

                                <Button variant={'success'} block onClick={this.setProxy}>Set Proxy</Button>                                 
                                                                                                                       
                            </Row>

                            <Row>
                              <Col md={4}><br /></Col>
                            </Row>                                                         


                    <Button href="/index.html#dashboard" style={{position: 'fixed', left: 0, bottom: 0}} variant="info"><i className="material-icons" style = {{color: "black"}}>arrow_back</i></Button>

            </div>
        );
    }
}

export default withCookies(withRouter(ProxySettings));    