/*global chrome*/
import React, {Component} from 'react';
import {Col, Row, Form, Button, Card, Alert} from "react-bootstrap";
import axios from "axios";
import { withCookies, Cookies } from 'react-cookie';
import {withRouter} from 'react-router-dom';
import Header from './Header';

const crypto = require('crypto');




class Dashboard extends Component {
    tmr;
    tmr2;
    profileSelect = React.createRef();
    state = {
        started: false,
        status: false,

        profileName: '',
        authString: '',
        login:'',

        currentUserProfile: {},
        userProfileInfo: [],
        userProfileName: '',
        userProfileReady: false,
        profilesLoaded: true,
        profileUrl:"",

        names: [],

        wait30second: false,
        toRemoveTab: '',
        fromBackground: {},
        licenseType: '',
        days: '',
        isLicenseActive: true,
        accessManageProfileFilesState: false,
        facebooktoken_ico: 'fb-mechanikus.png',
        edithtiscookie_ico: 'editthiscookie.png',


    };

    componentWillMount() {
       let authString = this.props.cookies.get('auth');
       if(authString !== undefined) {
           let login = this.props.cookies.get('login');
           this.setState({authString: authString, login: login});
       }
       else {
           this.props.history.push("/auth");
       }
       

    }

    componentDidMount() {
        this.becomeAllUserProfiles();
        this.checkCurrentUserProfileSelect();
        this.checkStatus();
        this.tmr = setInterval(this.checkStatus, 1000);
        this.license();        
    };

    license = async () => {
        await axios.post(process.env.REACT_APP_PUBLIC_API + '/api/vashi_documenti', {login: this.state.login}, {
            headers: {
                'Authorization': this.state.authString,
            }
        }).then(res => {

            this.setState({licenseType: res.data.license, days: res.data.days})
            if (res.data.days = 0) {
                this.setState({isLicenseActive: false})
            }
            if (res.data.days > 0) {
                this.setState({isLicenseActive: true})
            }
        })

        // this.licenseExpired();
        this.accessManageProfileFiles();       
    }

    // licenseExpired = () => {
    //     if (!this.state.isLicenseActive){
    //         this.props.history.push("/license_expired");
    //     }
    // }

    toggleChange30 = () => {
        this.setState({
          wait30second: !this.state.wait30second,
        });
    };

    checkStatus = () => {
        let status = localStorage.getItem('inProcess');
        if(status !== null) {
            status = status === 'true';
            if(this.state.status !== status) {
                this.setState({status: status});
            }

        }
    };

    checkCurrentUserProfileSelect = () => {
        let profile = JSON.parse(localStorage.getItem('userProfile'));
        if(profile !== null) {
            let name = profile.profileName;
            let login = profile.login;
            if(login === this.state.login) {
                let index = this.state.names.indexOf(name);
                this.profileSelect.current.selectedIndex = index + 1;
                this.setState(()=>({userProfileName: name, profileName: name}), ()=> {
                    this.setState({userProfileReady: true});
                });
                let started = localStorage.getItem('inProcess');
                if(started !== null) {
                    let start = started === 'true';
                    this.setState({started: start});
                }

            }

        }
    };

    getFbTools = () => {
        const url = '/index.html#facebook';
        window.open(url, '_self');
    };

    toGeoSettings = () => {
        const url = '/index.html#geosettings';
        window.open(url, '_self');
    };

    toProxySettings = () => {
        const url = '/index.html#proxysettings';
        window.open(url, '_self');
    };

    toBackgroundMessage = (object) => {
        chrome.runtime.sendMessage(object,
        function (response) {});
    }

    becomeUserProfile = () => {

        this.setState({profileReady: false}); 

        axios.post(process.env.REACT_APP_PUBLIC_API + '/api/get_current_user_profile', {
            'login': this.state.login,
            'profileName': this.state.userProfileName,
        }, {headers: {
                'Authorization' : this.state.authString,
            }}).then(async (res)=> {
                let d = res.data[0];
                let isProfileNew = this.checkProfileHash(JSON.stringify(d));
                if(isProfileNew) {
                d.method = 'messageBecomeUserProfile';
                await this.toBackgroundMessage(d);

                    this.setState( ()=> ({currentUserProfile: d, profileName: d.profileName}), ()=> {
                        this.start();
                    });

                }

            })
    };

    checkProfileHash = (str) => {
        let oldHash = localStorage.getItem('profileHash');
        const hash = crypto.createHash('sha256');
        hash.update(str);
        let hashStr = hash.digest('hex');
        if(oldHash !== hashStr) {
            localStorage.setItem('profileHash', hashStr);
            return true;
        }
        else {
            return false;
        }

    }    

    becomeAllUserProfiles = () => {

        axios.post(process.env.REACT_APP_PUBLIC_API + '/api/get_all_user_profiles', {
            'login': this.state.login,
        }, {headers: {
                'Authorization' : this.state.authString,
            }}).then((res)=> {
                let names = [];
                res.data.forEach((item)=> {
                    if(item.profileName !== undefined) {
                        names.push(item.profileName)
                    }
                });
            this.setState(()=>({userProfileInfo: res.data, names: names, profilesLoaded: true}), ()=> {
            });

        }).catch((err)=> {
            this.setState({profilesLoaded: false})
        })
    };

    saveSession = () => {
        if (JSON.parse(localStorage.getItem('userProfile')) !== null) {
            var toBackgroundMessage = {};
            toBackgroundMessage.method = 'messageSaveSession';
            chrome.runtime.sendMessage(toBackgroundMessage, function (response) {
            });
        }        
    }    


    changeForm = (e) => {
        let id = e.currentTarget.id;
        let value = e.currentTarget.value;

        switch (id) {
            case "users-profiles":
                this.setState(()=>({userProfileName: value}), ()=> {
                    let ready = value !== '';
                    this.setState({userProfileReady: ready})
                });
                break;
            default:
            break;
        }
    };

    start = () => {
        this.setState({started: true});
        localStorage.setItem('started', 'true');
        localStorage.setItem('inProcess', 'false');
    };

    stop = () => {
        this.setState({started: false});
        localStorage.setItem('started', 'false');
        var toBackgroundMessage = {};
        toBackgroundMessage.method = 'messageStopSession';
        chrome.runtime.sendMessage(toBackgroundMessage, function (response) {});        
    };

    onEnterAezakmi = (e) => {
        e.preventDefault();
        if(e.key === "Enter" && this.state.userProfileReady) {
            this.becomeUserProfile();
        }
    };

    onKey = (e) => {
        e.preventDefault();
    };

    accessManageProfileFiles = () => {
        if (this.state.licenseType == 'passenger' || this.state.licenseType == 'betting') {
            this.setState({accessManageProfileFilesState: false});
        }
        if (this.state.licenseType == 'grovestreet' || this.state.licenseType == 'cj' || this.state.licenseType == 'ballas'){
            this.setState({accessManageProfileFilesState: true});
        }
    }

    importCookie(){
        const url = '/index.html#uploadcookie';
        window.open(url, '_blank');
    }    
 
    
    render() {

        return (
            <div>
                <Header login = {this.state.login} />
                            <h4  className={'text-center'}>Select Profile</h4>
                            <Row className= "justify-content-center">
                            <Alert key="warning" variant="warning">
                                <span>Status: {!this.state.profilesLoaded ? <span className="text-danger">server is unavailable</span>
                                    : this.state.started && this.state.status ? <span className="text-success">Started</span> :
                                    this.state.started && !this.state.status ? <span className="text-success">Loading...</span> :
                                        <span className="text-danger">Stopped</span>}{!this.state.profilesLoaded ? "" :
                                    this.state.started && this.state.status ? ` with ${this.state.profileName}`:
                                    ''}</span> </Alert>
                                    <Alert className={this.state.isLicenseActive ? "d-none" : ""} key="warning" variant="warning">
                                    <span> Your License Expired! </span>
                                    </Alert>
                                    
                                <Col md = {4}>
                                    <Form.Control autoFocus={true}
                                                  onKeyPress={(e)=> this.onEnterAezakmi(e)}
                                                  ref = {this.profileSelect}
                                                  style={{width: "60%", display: "inline"}}
                                                  className = 'prof-select' value={this.state.userProfileName}
                                                  id='users-profiles' as="select" onChange={this.changeForm}>
                                        <option>{this.state.userProfileInfo.length === 0 && this.state.profilesLoaded ? '!!!You have no profiles!!!' : ''}</option>
                                        {this.state.userProfileInfo.length !== 0 ?
                                            this.state.names.map((item) => <option>{item}</option>)
                                            : null}
                                    </Form.Control>
                                    <Button onClick = {this.toGeoSettings} style={{marginLeft: "5%"}} variant={'secondary'}><i className="material-icons" style = {{color: "black"}}>translate</i></Button>
                                    <Button onClick = {this.toProxySettings} style={{marginLeft: "5%"}} variant={'secondary'}><i className="material-icons" style = {{color: "black"}}>wifi</i></Button>
                                    <Button variant={'success'} onKeyPress={(e)=> this.onEnterAezakmi(e)}  block disabled = {!this.state.userProfileReady} onClick = {this.becomeUserProfile}>A-E-Z-A-K-M-I</Button>
                                    
                                    <Button variant={'danger'} disabled = {!this.state.userProfileReady} style={{marginTop: 20, float: 'left', width: '160px'}} onClick = {this.stop}>Stop</Button>
                                    <Button target="_blank" variant={'info'} style={{marginTop: 20, marginLeft: 15, float: 'right', width: '140px'}} href="/index.html#aezakmi">Dashboard</Button>
                                    
                                    <Button target="_blank" className={!this.state.accessManageProfileFilesState ? "d-none" : ""} href="/index.html#saveprofile" style={{marginTop: 20, float: 'left', width: '160px'}} variant="dark">Save Profile File</Button>
                                    <Button target="_blank" className={!this.state.accessManageProfileFilesState ? "d-none" : "btn btn-dark"} style={{marginTop: 20, marginLeft: 15, float: 'right', width: '140px'}} href="/index.html#upload"> Upload Profile </Button>

                                    

                                    <Button target="_blank" style={{marginTop: 40, marginBottom: 20, float: "left", width: '160px'}} href="http://account.aezakmi.run" variant="dark">Create New Profile</Button>

                                    
                                    <div className={this.state.licenseType == 'betting' ? "d-none" : ""} style={{marginTop: 40, marginBottom: 20, marginLeft: 15, float: 'right', width: '140px'}}>
                                        <img style={{marginLeft: 15}} src={this.state.edithtiscookie_ico} onMouseEnter={() => {this.setState({edithtiscookie_ico: "editthiscookie_over.png"});}} onMouseOut={() => {this.setState({edithtiscookie_ico: "editthiscookie.png"});}} title="Import Cookies" onClick = {this.importCookie}/>
                                        <img style={{marginLeft: 40}} src={this.state.facebooktoken_ico} onMouseEnter={() => {this.setState({facebooktoken_ico: "fb-mechanikus_hower.png"});}} onMouseOut={() => {this.setState({facebooktoken_ico: "fb-mechanikus.png"});}} title="Facebook Tools" onClick = {this.getFbTools}/>
                                    </div>
          
                                </Col>
                </Row>
            </div>
        );
    }
}

export default withCookies(withRouter(Dashboard));
