/*global chrome*/
import {withRouter} from 'react-router-dom';
import React, {Component} from 'react';
import {Col, Row, Form, Button, Card, Alert} from "react-bootstrap";
import axios from "axios";
import { withCookies, Cookies } from 'react-cookie';
import Modal from 'react-bootstrap/Modal'



class UploadProfile extends Component {
	tmr;
    	state = {
            uploadStatus: false,
            profileName: '',
            authString: '',
            login:'',
        };


    componentWillMount() {
       let authString = this.props.cookies.get('auth');
       if(authString !== undefined) {
           let login = this.props.cookies.get('login');
           this.setState({authString: authString, login: login});
       }
       else {
           this.props.history.push("/auth");
       }

    }    


    UploadProfileFromFile = () => {

        // console.log('----------');
        // console.log('UploadProfileFromFile ON');
        // var timerAe = Math.floor(new Date().valueOf());
         

        let fileReader;
        const handleFileRead = (e) => {
            const content = fileReader.result;
            this.AfterUploadProfileFromFile(content);
        };
                
        const handleFileChosen = (file) => {
            fileReader = new FileReader();
            fileReader.onloadend = handleFileRead;
            fileReader.readAsText(file);
        };

        // console.log('UploadProfileFromFile OFF')
        // timerAe = Math.floor(new Date().valueOf()) - timerAe;
        // console.log('work time: ' + timerAe + ' milisecond')
        // timerAe = Math.floor(new Date().valueOf())
        

        return <input type='file'
                      id='file'
                      style={{position: 'fixed', bottom: 0, left: 0, opacity: 0, width: 0, height: 0}}
                      accept='.aezakmi'
                      onChange={e => handleFileChosen(e.target.files[0])}                  
                />      
    };

    UploadToBackground = async (object, string, boolean) => {
        // console.log('----------');
        // console.log('UploadToBackground ON');
        // console.log(object);
        // console.log(string);
        // var timerAe = Math.floor(new Date().valueOf());
        

        var toBackgroundMessage = object;
        toBackgroundMessage.method = 'messageUploadProfile';
        toBackgroundMessage.importedUserProfile = string;
        toBackgroundMessage.itIsNewProfile = boolean;

        // console.log('toBackgroundMessage: ');
        // console.log(toBackgroundMessage);

        await chrome.runtime.sendMessage(toBackgroundMessage, function (response) {});
        this.setState({
            uploadStatus: true,
            profileName: string});

        // console.log('UploadToBackground OFF');
        // timerAe = Math.floor(new Date().valueOf()) - timerAe;
        // console.log('work time: ' + timerAe + ' milisecond')
        // timerAe = Math.floor(new Date().valueOf())
                                              

    }

    randomInteger = (min, max) => {
      //    min  (max+1)
      let rand = min + Math.random() * (max + 1 - min);
      return Math.floor(rand);
    }



    AfterUploadProfileFromFile = async (object) => {
        // console.log('----------');
        // console.log('UploadProfileFromFile ON');
        // var timerAe = Math.floor(new Date().valueOf());
        

        var profileData = JSON.parse(atob(object));
        // console.log(profileData)
        var userProfile = JSON.parse(profileData['userProfile']);
        // console.log(userProfile)
        var userProfileName = userProfile['profileName'];
        // console.log(userProfileName)
        if (userProfile.login !== this.state.login) {
            userProfile.login = this.state.login;
            let random = this.randomInteger(100, 999)
            userProfile.profileName = userProfile.profileName + '_imported_' + random;
        }
        delete userProfile._id;

        //Callback HELL start
        axios.post(process.env.REACT_APP_PUBLIC_API + '/api/get_current_user_profile', {
            'login': this.state.login,
            'profileName': userProfileName,
        }, {headers: {'Authorization' : this.state.authString}}).then(async (res)=> {
                // console.log(res);
                //TODO          
                if (res.data.length == 0) {
                    axios.post(process.env.REACT_APP_PUBLIC_API + '/api/save_user_profile', {
                        'userProfile': userProfile,
                    }, {headers: {'Authorization' : this.state.authString}}).then(async (res)=> {
                        // console.log('SEND save_user_profile')
                        this.UploadToBackground(profileData, userProfile.profileName, true);

                            // console.log('UploadProfileFromFile OFF');
                            // timerAe = Math.floor(new Date().valueOf()) - timerAe;
                            // console.log('work time: ' + timerAe + ' milisecond')
                            // timerAe = Math.floor(new Date().valueOf())
                                                      
                    }) 
                } else {
                    this.UploadToBackground(profileData, userProfileName, false);
                            // console.log('DO NOT SEND save_user_profile')
                            // console.log('UploadProfileFromFile OFF');
                            // timerAe = Math.floor(new Date().valueOf()) - timerAe;
                            // console.log('work time: ' + timerAe + ' milisecond')
                            // timerAe = Math.floor(new Date().valueOf())
                                                   
                }                      
            })        
               

    };               

    render() {
        return(
        	<div className="bg">
		        <Modal show={true}>
			        <Modal.Header>
			          <Modal.Title>Upload Profile File</Modal.Title>
			        </Modal.Header>

		        	<Modal.Body>
		        		Select your profile file.
		        		<Alert key="warning" variant="warning">
                            <span>Status: {this.state.uploadStatus ? <span className="text-success">Success upload profile</span> : <span className="text-danger">Not files to upload</span>}
                            {this.state.profileName != '' ? ` with ${this.state.profileName} name`: ''}</span> 
                        </Alert>
		        		<label for="file" block className="btn btn-dark">Upload Profile File</label>
						{this.UploadProfileFromFile()}
		        	</Modal.Body>

			        <Modal.Footer>
			        </Modal.Footer>
		      	</Modal>
        	</div>
        );
    }
}


export default withCookies(withRouter(UploadProfile));
